.App {
    text-align: center;
  }
  
  .product-list, .shop-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;
  }
  
  .product {
    border: 1px solid #ddd;
    padding: 20px;
    margin: 10px;
    width: 200px;
  }
  
  .product h2 {
    font-size: 18px;
    margin: 0 0 10px;
  }
  
  .product p {
    font-size: 14px;
    margin: 5px 0;
  }
  
  button {
    background-color: #28a745;
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    font-size: 14px;
  }
  
  button:hover {
    background-color: #218838;
  }
  
  .shop-list .product button {
    background-color: #dc3545;
  }
  
  .shop-list .product button:hover {
    background-color: #c82333;
  }
  